"use client";

import Link from "next/link";
import { ShoppingBag, Menu, X, ArrowRight, User } from "lucide-react";
import { useState } from "react";
import { useCart } from "@/context/CartContext"; // <--- CONNECTS TO GLOBAL CART

export default function Navbar() {
  const { cartCount, openCart } = useCart();
  const [isMenuOpen, setIsMenuOpen] = useState(false);

  return (
    <nav className="border-b border-gray-100 sticky top-0 bg-white/80 backdrop-blur-xl z-50 transition-all duration-300">
      <div className="max-w-7xl mx-auto px-4 h-16 flex items-center justify-between">
        
        {/* LOGO */}
        <Link href="/" className="flex items-center gap-2 group">
          <div className="bg-gray-900 text-white w-8 h-8 rounded-lg flex items-center justify-center group-hover:bg-emerald-600 transition duration-300 shadow-sm">
            <ShoppingBag size={16} />
          </div>
          <span className="font-extrabold text-lg tracking-tight text-gray-900">StoreLink</span>
        </Link>

        {/* DESKTOP LINKS */}
        <div className="hidden md:flex items-center gap-6">
           <Link href="/marketplace" className="text-sm font-bold text-gray-600 hover:text-gray-900 transition">
             Marketplace
           </Link>
           <Link href="/login" className="text-sm font-bold text-gray-600 hover:text-gray-900 transition flex items-center gap-1">
             <User size={16} /> Login
           </Link>
           <Link href="/signup" className="flex items-center gap-2 px-5 py-2.5 bg-gray-900 text-white rounded-xl text-sm font-bold hover:bg-gray-800 transition shadow-lg hover:shadow-xl hover:-translate-y-0.5 transform duration-200">
             Start Selling <ArrowRight size={14} />
           </Link>
           
           {/* Cart Button (Only shows if items exist) */}
           {cartCount > 0 && (
             <button 
               onClick={openCart} 
               className="relative p-2.5 bg-gray-100 hover:bg-gray-200 rounded-full transition group"
             >
               <ShoppingBag size={20} className="text-gray-900 group-hover:scale-110 transition" />
               <span className="absolute -top-1 -right-1 bg-red-500 text-white text-[10px] font-bold h-5 w-5 flex items-center justify-center rounded-full border-2 border-white shadow-sm animate-in zoom-in">
                 {cartCount}
               </span>
             </button>
           )}
        </div>

        {/* MOBILE CONTROLS */}
        <div className="flex items-center gap-4 md:hidden">
          {/* Mobile Cart Button */}
          {cartCount > 0 && (
             <button onClick={openCart} className="relative p-1">
               <ShoppingBag size={24} className="text-gray-900" />
               <span className="absolute -top-1 -right-1 bg-red-500 text-white text-[10px] font-bold h-4 w-4 flex items-center justify-center rounded-full border-2 border-white">
                 {cartCount}
               </span>
             </button>
           )}
          
          {/* Menu Toggle */}
          <button 
            onClick={() => setIsMenuOpen(!isMenuOpen)} 
            className="p-2 text-gray-900 focus:outline-none"
          >
            {isMenuOpen ? <X size={24} /> : <Menu size={24} />}
          </button>
        </div>
      </div>
      
      {/* MOBILE MENU DROPDOWN */}
      {isMenuOpen && (
        <div className="md:hidden absolute top-16 left-0 w-full bg-white border-b border-gray-100 shadow-xl animate-in slide-in-from-top-5 z-40">
           <div className="flex flex-col p-4 space-y-4">
             <Link href="/marketplace" className="flex items-center gap-3 p-3 rounded-xl hover:bg-gray-50 text-sm font-bold text-gray-700">
                <ShoppingBag size={18} /> Shop Marketplace
             </Link>
             <Link href="/login" className="flex items-center gap-3 p-3 rounded-xl hover:bg-gray-50 text-sm font-bold text-gray-700">
                <User size={18} /> Vendor Login
             </Link>
             <Link href="/signup" className="block w-full py-4 bg-gray-900 text-white rounded-xl text-sm font-bold text-center shadow-md">
               Create My Store
             </Link>
           </div>
        </div>
      )}
    </nav>
  );
}